import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { searchDog } from "../actions";
import style from "./SearchBar.module.css";

export default function SearchBar() {
  const [search, setSearch] = useState();

  const dispatch = useDispatch();

  function redirect() {
    window.location.href = "http://localhost:3000/home";
  }
  let x = true;
  // async await me trae el estado actualizado, si no me trae "Houn" en vez de "Hound"
  const handleChange = async (e) => {
    await setSearch(e.target.value);
    if (
      e.target.value === "x" ||
      e.target.value === null ||
      e.target.value === "" ||
      e.target.value === undefined
    )
      x = false;
    else x = true;
  };
  const handleSubmit = (e) => {
    if (x) {
      e.preventDefault();
      dispatch(searchDog(search));
      setSearch("");
    } else redirect();
  };

  const dogers = useSelector((state) => state.allDogs);

  return (
    <div className={style.search}>
      <form>
        <select
          className={style.input}
          type="text"
          value={search}
          onChange={(e) => handleChange(e)}
        >
          <option value="x">Insert a breed</option>;
          {dogers?.map((t) => {
            return <option value={t.name}>{t.name}</option>;
          })}
        </select>
        <button
          className={style.button}
          type="submit"
          onClick={(e) => handleSubmit(e)}
        >
          Search
        </button>
      </form>
    </div>
  );
}

module.export = SearchBar;
