import React from "react";
import { Link } from "react-router-dom";
import style from "./Landing.module.css";

const welcome =
  "https://i.pinimg.com/originals/93/f8/9b/93f89b965b719a175e2ac7de6c3e8b54.gif";
const letsGo =
  "https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/bd6cf7cc-889b-479b-8be6-267117372f34/d689ee3-d2f69d93-575a-461e-b590-20e43e0b0a93.png/v1/fill/w_500,h_500,strp/texto_png_lets_go__by_infinitylovato_d689ee3-fullview.png?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..mPY5gr0RUScD2AjQLdPUF5QaqrIquJ1bEpDQnJMNyhc";
const url =
  "https://s1.eestatic.com/2021/11/10/actualidad/626198188_214456909_1024x576.jpg";
const url1 = "http://c.files.bbci.co.uk/48DD/production/_107435681_perro1.jpg";
const url2 =
  "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQdEihCb3vxmwGa7PGfdzvfWPCJNJZQeY8gR2JWcSnD&s";
const url3 =
  "https://images.ecestaticos.com/H4CUeGslvPRglrmCtaP7A4wz9KQ=/0x115:2265x1390/1600x900/filters:fill(white):format(jpg)/f.elconfidencial.com%2Foriginal%2F8ec%2F08c%2F85c%2F8ec08c85c866ccb70c4f1c36492d890f.jpg";

const Landing = () => {
  return (
    <>
      <div className={style.background}>
        <img className={style.imge} src={welcome} alt="Not found" />
        <Link exact to="/home">
          <img className={style.img} src={letsGo} alt="Not found" />
        </Link>
        <div>
          <img className={style.img} src={url} alt="Not found" />
          <img className={style.img} src={url1} alt="Not found" />
          <img className={style.img} src={url2} alt="Not found" />
          <img className={style.img1} src={url3} alt="Not found" />
        </div>
      </div>
    </>
  );
};

export default Landing;
